.container-fluid.container-fluid-max {
	max-width: 1920px; }


.badge {
	--bs-badge-font-weight: 700;
	--bs-badge-padding-x: #{rem(6px)};
	--bs-badge-padding-y: #{rem(3px)};
	--bs-badge-font-size: #{rem(10px)};
	--bs-badge-border-radius: #{rem(3px)};
	--bs-badge-color: var(--bs-body-color);
	text-transform: uppercase;
	line-height: 1.1;
	&.bg-danger {
		--bs-badge-color: var(--bs-white); } }
