.sidebar-menu {
	z-index: 9;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background-color: var(--bs-gray-900);
	display: flex;
	visibility: hidden;
	opacity: 0;
	& &__wrapper {
		height: 100%;
		padding: rem(50px);
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center; }
	& &__category {
		&-list {}
		&-li {}
		&-link {
			color: var(--bs-white);
			font-size: rem(30px);
			font-weight: 500;
			padding: rem(10px) 0;
			display: inline-block;
			&:hover {
				color: var(--bs-link-color); }
			.icon {
				--sz: #{rem(30px)};
				top: rem(-2px); } } } }


.toggle-sidebar {
	overflow: hidden;
	.nav-base {
		background-color: var(--bs-gray-900);
		transition: .0s .35s;
		box-shadow: 0 0 rem(5px) rem(5px) var(--bs-gray-900); }
	.sidebar-menu {
		visibility: visible;
		overflow-x: auto;
		opacity: 1; }
	.toggle-sidebar-item {
		&.btn {
			--bs-btn-color: var(--bs-white); }
		.logo-img {
			&--black {
				opacity: 0; }
			&--white {
				opacity: 1; } } } }
