$border-color: #dde8ec;
$black:   #000000;
$white:   #FFFFFF;
// $blue:    #b6e4ce
$blue:    #cde9d4;
$indigo:  #6610f2;
$purple:  #6f42c1;
$pink:    #d63384;
$red:     #FFCEBE;
$orange:  #fd7e14;
$yellow:  #fee366;
$green:   #b0f4d0;
$teal:    #20c997;
$cyan:    #cbe0ff;

$font-family-sans-serif: 'Montserrat', sans-serif;
$small-font-size:     13px;
$container-padding-x: 140px;
$bdrs-lg:             20px;
$icon-padding:        5px;
$body-bg:             $white;

$nav-size:          90px;
$sidebar-size:      240px;
$sidebar-cart-size: 350px;

$logo-size: 150px;
$logo-size-sm: 100px;

$component-active-color: $black;


// Border radius
$border-radius:      .375rem;
$border-radius-sm:   .25rem;
$border-radius-lg:   .5rem;
$border-radius-xl:   1rem;
$border-radius-2xl:  2rem;
$border-radius-pill: 50rem;

// Links
$link-color-deafult:    #495353;
$link-color:            #38b2b5;
$link-decoration:       none;
$link-shade-percentage: 4%;
$link-hover-color:      #14797c;
$link-hover-decoration: none;



// Forms
$form-check-input-width: 1.25em;
$form-check-input-border: 0.0625rem solid shade-color($border-color, 12%);
$form-check-input-checked-bg-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'><path fill='none' stroke='#{$component-active-color}' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.9' d='m6 10 3 3 6-6'/></svg>");
$form-check-radio-checked-bg-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'><circle r='1.2' fill='#{$component-active-color}'/></svg>");

// scss-docs-start form-label-variables
$form-label-margin-bottom: rem(5px);
$form-label-font-size:     $small-font-size;
$form-label-font-weight:   500;
$form-label-color:         tint-color($black, 55%);

// scss-docs-start form-text-variables
$form-text-margin-top:     rem(5px);
$form-text-font-size:      $small-font-size;
$form-text-font-weight:    400;
$form-text-color:          tint-color($black, 50%);

// h1-h6 font size
$h1-font-size: rem(50px);
$h2-font-size: rem(42px);
$h3-font-size: rem(34px);
$h4-font-size: rem(26px);
$h5-font-size: rem(20px);
$h6-font-size: rem(16px);
