// Bootstrap mixins
@import "../../node_modules/bootstrap/scss/functions";


// CONVERTING PX TO REM
@function rem($size) {
	@return ($size / 16px) + rem; }


// Dark / Light mode
@mixin dark {
	body.dark {
		@content; } }


// Responsive breakpoints
@mixin r($width) {
	@media only screen and (max-width: $width+ "px") {
		@content; } }

@mixin rmin($width) {
	@media only screen and (min-width: $width+ "px") {
		@content; } }

@mixin xxl {
	@include r(1400) {
		@content; } }

@mixin xl {
	@include r(1200) {
		@content; } }

@mixin lg {
	@include r(992) {
		@content; } }

@mixin md {
	@include r(768) {
		@content; } }

@mixin sm {
	@include r(576) {
		@content; } }
